<template>
  <div>
    <el-container>
      <el-aside style="width: 200px;min-height: 100vh ;background-color: #001529">
        <div style="height: 60px;color: white;display:flex; align-items: center;justify-content: center">
          企业工作台
        </div>
        <el-menu :default-active=$route.path active-text-color="#fff" background-color="#001529" router
                 style="border: none;background-color: red" text-color="rgba(255,255,255,0.65)">
          <el-menu-item index="/componentHomeView">
            <template slot="title">
              <i class="el-icon-house"></i>
              <span>系统首页</span>
            </template>
          </el-menu-item>
          <el-menu-item index="/componentInterview">面试通知</el-menu-item>

          <el-submenu>
            <template slot="title">
              <span>信息管理</span>
            </template>
            <el-menu-item index="companyInformation">查看/修改企业信息</el-menu-item>
            <el-menu-item index="componentPosts">查看所有岗位</el-menu-item>
            <el-menu-item index="companyPost">发布岗位</el-menu-item>
          </el-submenu>
          <el-menu-item index="/">用户退出</el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header></el-header>
        <el-main>
          <el-table border :data="interview">
            <el-table-column label="姓名" prop="userName"/>
            <el-table-column label="招聘岗位" prop="recruitmentPosition"/>

            <el-table-column label="面试状态" prop="status"/>
            <el-table-column label="操作" align="center">
              <template slot-scope="scope">
                <el-button type="primary" @click="view(scope.row.userId)">查看求职者简历</el-button>
                <el-button type="success" @click="consent(scope.row.id)">同意</el-button>
                <el-button type="warning" @click="refuse(scope.row.id)">拒绝</el-button>
              </template>

            </el-table-column>
          </el-table>
        </el-main>
      </el-container>

    </el-container>


  </div>
</template>
<style>
.el-menu--inline .el-menu-item {
  background-color: #000c17 !important;
}

.el-menu-item:hover, .el-submenu__title:hover {
  color: #fff !important;
}

.el-submenu__title:hover .el-icon-menu {
  color: #fff !important;
}

.el-menu-item.is-active {
  background-color: red !important;
}


</style>


<script>
import request from "@/utils/request";

export default {
  name: 'componentInterview',
  data() {
    return {
      interview: []
    }
  },
  methods: {
    view(userId) {
      this.$router.push({
        path: '/viewUser',
        query: {
          userId
        }
      })
      this.$message.success("查看成功")
    }, consent(id) {
      request.get('/component/consent/' + id).then(res => {
        location.reload()
      })
    }, refuse(id) {
      request.get('/component/refuse/' + id).then(res => {
        location.reload()
      })
    }
  },
  mounted() {
    request.get('/component/interview').then(res => {
      console.log(res.data.data);
      this.interview = res.data.data
      let length = this.interview.length;

      for (let i = 0; i < length; i++) {
        if (res.data.data[i].status === 0) {
          this.interview[i].status = '未处理'
        }
        if (res.data.data[i].status === 1) {
          this.interview[i].status = '同意'
        }
        if (res.data.data[i].status === 2) {
          this.interview[i].status = '拒绝'
        }
      }

    })
  }
}
</script>
